.projects {
    background-color: #fff;
    padding: 60px 20px;
    text-align: center;
  }
  
  .projects .container {
    max-width: 800px;
    margin: 0 auto;
  }
  
  .projects h2 {
    font-size: 2.5em;
    margin-bottom: 40px;
    color: #333;
  }
  
  .project-item {
    background-color: #f0f4f8;
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 20px;
    margin: 20px 0;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .project-item h3 {
    font-size: 1.8em;
    margin-bottom: 10px;
    color: #007BFF;
  }
  
  .project-item p {
    font-size: 1.1em;
    color: #555;
    line-height: 1.6;
  }
  