header {
  background-color: #333;
  color: white;
  padding: 10px 0;
  text-align: center;
  position: relative;
}

header h1 {
  margin: 0;
  font-size: 2rem; /* Adjust font size as needed */
}

nav {
  margin-top: 40px; /* Increase space between the heading and the nav */
}

nav button {
  margin: 0 10px;
  padding: 10px 20px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

nav button:hover {
  background-color: #2980b9;
}
