/* Header section */
header {
  background-color: #333;
  color: white;
  padding: 10px 0;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
}

header h1 {
  margin: 0;
  text-align: center;
  font-size: 1.5rem; /* Adjust as needed */
}

nav {
  margin-top: 15px; /* Adjust margin if needed */
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

nav li {
  margin: 0 10px; /* Reduce margin for closer spacing */
}

button {
  background: #555; /* Button background color */
  border: 2px solid transparent; /* Transparent border initially */
  color: white;
  font-size: 1rem;
  cursor: pointer;
  padding: 10px 20px; /* Button padding */
  border-radius: 5px; /* Rounded corners */
  transition: background 0.3s ease, border-color 0.3s ease; /* Smooth transition for hover */
}

button:hover {
  background: #666; /* Darker background on hover */
  border-color: #ddd; /* Border color on hover */
  text-decoration: none; /* Ensure no underline */
}

button:focus {
  outline: none; /* Remove default focus outline */
}

button.active {
  background: #3498db; /* Highlight color for the active button */
  border-color: #3498db; /* Border color for the active button */
}
