.about {
  padding: 50px 0;
  background-color: #f9f9f9;
  color: #333;
}

.about .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.about h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #2c3e50;
}

.about-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
}

.about-content img {
  width: 350px;  /* Adjust width as needed */
  height: 400px;  /* Maintain aspect ratio */
  border-radius: 8px; /* Optional: rounded corners */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3); /* Adding shadow for emphasis */
  transition: transform 0.3s ease, box-shadow 0.3s ease; /* Smooth scaling and shadow effects */
}

.about-content img:hover {
  transform: scale(1.05); /* Slight zoom effect on hover */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.4); /* Enhanced shadow on hover */
}

.about-text {
  flex: 1;
  margin-left: 20px; /* Space between image and text */
  text-align: left;
}

/* Animation styles */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in {
  opacity: 0;
  animation: fadeInUp 0.6s ease-out forwards;
}

.detail {
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  padding: 20px;
  flex: 1;
  min-width: 250px; /* Minimum width for each detail box */
  margin: 10px;
  transition: transform 0.3s ease, box-shadow 0.3s ease, background-color 0.3s ease; /* Added background color transition */
}

.detail:hover {
  transform: translateY(-10px); /* Lift effect on hover */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3); /* Enhanced shadow on hover */
  background-color: #eaf4ff; /* Light blue background color on hover */
}

.detail h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
  color: #3498db;
  transition: color 0.3s ease; /* Smooth color transition */
}

.detail:hover h3 {
  color: #1e70c0; /* Darker blue color on hover */
}

.detail ul {
  list-style: none;
  padding: 0;
}

.detail li {
  margin-bottom: 10px;
}

.detail strong {
  color: #2c3e50;
}

@media (max-width: 768px) {
  .about-content {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .about-details {
    flex-direction: column;
    align-items: center;
  }
}
