.contact {
  background-color: #f9f9f9;
  padding: 60px 20px;
  text-align: center;
}

.contact .container {
  max-width: 800px;
  margin: 0 auto;
}

.contact h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
  color: #333;
}

.contact p {
  font-size: 1.2em;
  margin-bottom: 40px;
  color: #555;
}

.contact-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.contact-item {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
}

.contact-item h3 {
  font-size: 1.5em;
  margin-bottom: 10px;
  color: #007BFF;
}

.contact-item p {
  font-size: 1.1em;
  color: #555;
}

.contact-item a {
  color: #007BFF;
  text-decoration: none;
}

.contact-item a:hover {
  text-decoration: underline;
}
