.skills {
  background-color: #f9f9f9;
  padding: 60px 20px;
  text-align: center;
  overflow: hidden; /* Ensure content doesn't overflow during animation */
}

.skills .container {
  max-width: 800px;
  margin: 0 auto;
}

.skills h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
  color: #333;
}

/* Define animation keyframes for sliding up effect */
@keyframes slideInUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Apply the animation to skill items */
.skill-item {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 20px;
  margin: 10px;
  flex: 1 1 250px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
  opacity: 0; /* Initially hide the element */
  transform: translateY(20px); /* Start position for the animation */
  animation: none; /* No animation initially */
}

.skill-item.visible {
  animation: slideInUp 0.6s forwards; /* Apply the animation when visible */
}

/* Hover effect for skill items */
.skill-item:hover {
  background-color: #e0f7fa; /* Change background color on hover */
  transform: scale(1.05); /* Slightly scale up the item */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Increase shadow on hover */
}

.skill-item h3 {
  font-size: 1.5em;
  margin-bottom: 15px;
  color: #007BFF;
}

.skill-item ul {
  list-style: none;
  padding: 0;
}

.skill-item ul li {
  font-size: 1.2em;
  margin: 5px 0;
  color: #555;
}
