.profile {
    background-color: #f4f4f4;
    padding: 60px 20px;
    text-align: center;
  }
  
  .profile .container {
    max-width: 800px;
    margin: 0 auto;
  }
  
  .profile-picture {
    cursor: pointer;
    display: inline-block;
    transition: transform 0.3s;
  }
  
  .profile-picture img {
    border-radius: 50%;
    width: 150px;
    height: 150px;
    object-fit: cover;
  }
  
  .profile-picture:hover {
    transform: scale(1.1);
  }
  
  .profile-details {
    margin-top: 20px;
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    animation: fadeIn 0.6s ease-in-out;
  }
  
  .profile-details h2 {
    font-size: 2em;
    color: #007BFF;
  }
  
  .profile-details h3 {
    font-size: 1.5em;
    margin-top: 20px;
  }
  
  .profile-details ul {
    list-style: none;
    padding: 0;
  }
  
  .profile-details ul li {
    font-size: 1.2em;
    margin: 5px 0;
    color: #555;
  }
  
  /* Define keyframes for fade-in animation */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  